import Web3 from "web3";
import Web3Connect from "web3connect";
import WalletConnectProvider from "@walletconnect/web3-provider";



const first_contract_json = require('./idodo.json');


class IDODOContractWeb3 {
  constructor() {
    this.contract_addr = '0x5c30dAf600cBb5Ba2b5701364b93361858d781d2';
    this.from_addr = '';
    this.share_link = "";
    this.inviteBalancece = 0;
    this.myinviters = [];
    this.selfAirdropCount = 0;
    this.inviteAirdropCount = 0;
    this.decimal = 0;
    this.init();
    // this.init_contract_web3_deploy_1_0_wersion();
    // this.init_contract_web3_at_1_0_wersion();
  }

  // web3对象初始化(仅兼容web3.js 1.0版本)
  init() {
    let _this = this;
    const web3Connect = new Web3Connect.Core({
      network: "mainnet", // optional
      cacheProvider: true,
      providerOptions: {
        walletconnect: {
          package: WalletConnectProvider, // required
          options: {
            infuraId: "edfaaf5d51f440858905cb6d11cb18f2" // required
          }
        }
      }
    });

    // subscribe to connect
    web3Connect.on("connect", (provider) => {
      _this.web3 = new Web3(provider); // add provider to web3
      // 基于原生的web3.eth.Contract对象来创建合约（调用合约的方法）
      let contract_instance = new _this.web3.eth.Contract(first_contract_json.abi, _this.contract_addr);
      console.log('contract_instance合约实例：', contract_instance);
      _this.contract_instance = contract_instance;
      _this.from_addr = provider.selectedAddress;
      _this.initData();
      provider.on('accountsChanged', function () {
        // Time to reload your interface with accounts[0]!
        _this.from_addr = provider.selectedAddress;
        _this.initData();
      })
    });

    // subscribe to close
    web3Connect.on("close", () => {
      console.log("Web3Connect Modal Closed"); // modal has closed
    });

    web3Connect.toggleModal(); // open modal on button click
  }

  initData() {
    this.myInviteCode();
    this.getInviteBalancece();
    this.getMyinviters();
    this.getSelfAirdropCount();
    this.getInviteAirdropCount();
    this.decimals();
  }

  listening_web3() {
    this.web3.eth.net.isListening().then((result) => {
      console.log('您正在监听网络：', result);
    });
    this.web3.eth.net.getId().then((result) => {
      console.log('您当前网络ID：', result);
    });
  }

  event_contract(contract_instance) {
    contract_instance.events.eventInfo((error, event) => {
      // 正常情况下这里的(error, event)不会返还任何数据！
      console.log('eventInfo', error, event);
    }).on('data', (result) => {
      // 这里返回事件的数据,并存放在数组returnValues对象中！
      console.log('data', result.returnValues);
    }).on('changed', (result) => {
      console.log('changed', result);
    }).on('error', console.error);
  }

  /**
   * 1、初始化web3.eth.Contract合约
   * 2、直接调用truffle手动部署成功的合约实例(合约地址)
   * 注意：from发送账户务必注意两点：1、必须存在于区块链上; 2、浏览器访问Metamask的账户必须是发送地址账户！
   */

  myInviteCode() {
    // 调用solidity合约的只读方法，并在EVM中直接执行方法，不需要发送任何交易。因此不会改变合约的状态。
    let _this = this;
    this.contract_instance.methods.myInviteCode().call({
      from: this.from_addr
    }).then((result) => {
      console.log('myInviteCode已经执行了');
      console.log(result);
      _this.share_link = result;
    });
  }

  getAirDrop(inviterCode) {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    inviterCode = inviterCode || 0;
    this.contract_instance.methods.getAirDrop(inviterCode).send({
      from: this.from_addr
    }).then((result) => {
      console.log('getAirDrop已经执行了');
      console.log('getAirDrop返回对象:', result);
    });

    /**
     * 监听solidity事件一定是先触发在监听的原则
     * 务必注意：合约方法内部触发的事件,web3.js先调用合约方法,才能监听到对应的合约事件
     * 比如说：在合约内部setInfo方法调用了eventInfo事件，那么web3.js必须先调用了setInfo方法后,再监听events
     */
    // this.event_contract(this.contract_instance);
  }

  takeInviteBalancece() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    this.contract_instance.methods.takeInviteBalancece().send({
      from: this.from_addr
    }).then((result) => {
      console.log('takeInviteBalancece已经执行了');
      console.log('takeInviteBalancece返回对象:', result);
    });
  }
  getInviteBalancece() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    let _this = this;
    this.contract_instance.methods.getInviteBalancece().call({
      from: this.from_addr
    }).then((result) => {
      console.log('getInviteBalancece已经执行了');
      console.log('getInviteBalancece返回对象:', result);
      _this.inviteBalancece = result;
    });
  }

  getMyinviters() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    let _this = this;
    this.contract_instance.methods.getMyinviters().call({
      from: this.from_addr
    }).then((result) => {
      console.log('getMyinviters已经执行了');
      console.log('getMyinviters返回对象:', result);
      _this.myinviters = result;
    });
  }

  getSelfAirdropCount() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    let _this = this;
    this.contract_instance.methods.getSelfAirdropCount().call({
      from: this.from_addr
    }).then((result) => {
      console.log('getSelfAirdropCount已经执行了');
      console.log('getSelfAirdropCount返回对象:', result);
      _this.selfAirdropCount = result;
    });
  }
  getInviteAirdropCount() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    let _this = this;
    this.contract_instance.methods.getInviteAirdropCount().call({
      from: this.from_addr
    }).then((result) => {
      console.log('getInviteAirdropCount已经执行了');
      console.log('getInviteAirdropCount返回对象:', result);
      _this.inviteAirdropCount = result;
    });
  }
  decimals() {
    // 向solidity合约发送交易来执行指定方法，将改变合约的状态(必须输入发送交易的账户地址,需要消耗gas)
    let _this = this;
    this.contract_instance.methods.decimals().call({
      from: this.from_addr
    }).then((result) => {
      console.log('decimals已经执行了');
      console.log('decimals返回对象:', result);
      _this.decimal = result;
    });
  }
}

export default new IDODOContractWeb3();